.contacts{
  .section-title {
    color: $color-light;
    max-width: 280px;
  }

  .contacts__link{
    @include georgia-normal();
    font-size: 20px;
    line-height: 115%;
    letter-spacing: 0.06em;
    color: $color-primary;
    text-decoration: none;
    display: flex;
    align-items: center;

    &:before{
      content: '';
      display: inline-block;
      margin-right: 20px;
      background-repeat: no-repeat;
      background-position: center;
      background-size: auto;
    }

    &_mail:before {
      min-width: 40px;
      min-height: 32px;
      background-image: url('data:image/png;base64,');
    }

    &_phone:before {
      min-width: 40px;
      min-height: 32px;
      background-image: url('data:image/png;base64,');
    }

    &_spot:before {
      min-width: 22px;
      min-height: 32px;
      background-image: url('data:image/png;base64,');
    }
  }

  &:first-child {
    margin-right: 160px;
    .section-title{
      margin-bottom: 43px;
    }
    & .contacts__link{
      margin-bottom: 43px;
      position: relative;
      top: -6px;
    }
  }

  &:last-child {
    .section-title{
      margin-bottom: 33px;
    }
    & .contacts__link{
      margin-bottom: 33px;
    }
  }
}